.Drawer {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 400px;
    padding: 20px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: white;
    -webkit-transform: translateX(0px);
        -ms-transform: translateX(0px);
            transform: translateX(0px);
    -webkit-transition: -webkit-transform 0.22s ease-in;
    transition: -webkit-transform 0.22s ease-in;
    -o-transition: transform 0.22s ease-in;
    transition: transform 0.22s ease-in;
    transition: transform 0.22s ease-in, -webkit-transform 0.22s ease-in;
    z-index: 90;
}

.Drawer.close {
    -webkit-transform: translateX(-400px);
        -ms-transform: translateX(-400px);
            transform: translateX(-400px);
}

.Drawer ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.Drawer ul li {
    margin-bottom: 15px;
    padding-left: 10px;
}

.Drawer ul li a {
    color: black;
    font-size: 35px;
    text-decoration: none;
    /* background-color: grey; */
    position: relative;
    padding: 0px 20px 10px 20 px;
    -webkit-transition: opacity 0.15s;
    -o-transition: opacity 0.15s;
    transition: opacity 0.15s
}

.Drawer ul li a:hover, .active {
    opacity: .4;
}




