.Quiz {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, left top, right top, from(rgb(4, 110, 117)), to(rgb(177, 233, 139)));
    background: -webkit-linear-gradient(left, rgb(4, 110, 117) 0%, rgb(177, 233, 139) 100%);
    background: -o-linear-gradient(left, rgb(4, 110, 117) 0%, rgb(177, 233, 139) 100%);
    background: linear-gradient(90deg, rgb(4, 110, 117) 0%, rgb(177, 233, 139) 100%);
}

.Quiz h1 {
    color: rgb(40, 32, 70);
    margin-left: 10px;
}

.QuizWrapper {
    width: 600px;
}


